    margin: 0 130px 0 0;
    &:last-child {
      margin: 0;
    }
  }

  @media (max-width: 992px) {

    .footer .footer-img {
      margin: 0;
    }
    .footer .footer-list {
      margin: 0;
      padding: 0 10px;
    }

    .fixed-menu .sub-list .slide-two {
      &::after {
        left: unset;
        right: 15px;
      }
      &.active {
        &::after {
          content: "";
        }
      }
    }
    .fixed-menu .menu {
      left: -280px;
      right: unset;
      &.show {
        left: 0;
        right: unset;
      }
    }
    .fixed-menu .menu-header .close {
      left: unset;
      right: 15px;
    }
    .fixed-menu .menu-header .lang-switcher {
      right: unset;
      left: 0;
    }
