      left: 65px;
    }
    .fixed-menu .main-list .slide::after {
      left: unset;
      right: 10px;
    }
    .fixed-menu .main-list .slide.active::after {
      content: "";
    }
    .footer .list-header::after {
      left: unset;
      right: 0;
    }
    .footer .list-header.active {
      &::after {
        content: "";
      }
